html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

body {
  margin: 0;
}

.wrapper {
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
}

.calendar {
  flex-wrap: wrap;
  gap: 1px;
  width: 728px;
  padding: 10px;
  font-family: Arial, sans-serif;
  display: flex;
  transform: translateX(1px);
}

.calendar__day {
  box-sizing: border-box;
  background-color: #eee;
  border: 1px solid #000;
  width: 100px;
  height: 100px;
  margin: 0;
  padding: 0;
  font-size: 30px;
  transition: transform .5s;
  display: none;
  position: relative;
}

.calendar__day:hover {
  cursor: pointer;
  background-color: #ffbfcb;
  transform: translateY(-20px);
}

